.main {
  width: 100%;
  padding: 0 10px;
  position: absolute;
  top: 0;
  left: 0;
}

nav {
  width: 90%;
  max-height: 60px;
  top: 0;
  left: 0;
  position: absolute;
  display: flex;
  margin: 20px 5%;
}

.btn {
  padding: 15px 35px;
  border-radius: 7px;
  border: 0;
  outline: 0;
  font-size: larger;
} 

.bg-Dark {
  background-color: #000;
  color: #fff;
}

.divNavLogo {
  position: absolute;
  width: 40%;
  height: 60px;
}

.navLogo {
  max-height: 100%;
  width: -moz-fit-content;
  width: auto;
  top: 50%;
  transform: translate(0, -50%);
  position: absolute;
}

.divNavContent {    
  display: flex;
  line-height: 60px;
  width: -moz-fit-content;
  width: fit-content;
  position: absolute;
  right: 0;
}

.divNavParts {  
  margin: 0 15px;
}

.sec1 {
  position: absolute;
  background-image: url('./assets/images/background/sec1BG.png');
  background-size: 100% 100%;
  width: 100%;
  left: 0;
  top: 0;
}

.secHeader {
  color: #0d53b4;
  font-size: 60px;
  font-family: serif;
  margin-bottom: 0;
  text-align: center;
  width: 60%;
  margin-left: 20%;
  margin-top: 200px;
}

.secSubHeader {
  font-size: 60px;
  font-family: serif;
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
  width: 60%;
  margin-left: 20%;
}

.secDescription {
  text-align: center;
  width: 60%;
  margin-left: 20%;
  color: gray;
  margin-bottom: 70px;
}

.sec2 {  
  position: absolute;
  width: 100%;
  top: 500px;
  left: 0;
}

.sec2BG {
  width: 100%;
  height: auto;
}

.middleBar {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  padding: 15px;
  background-color: #fff;
  border-radius: 5px;  
  box-shadow: 10px 10px 20px #9c9c9c;
}

.barContent1, .barContent2, .barContent3 {
  padding: 0 20px;
  border-right: 1px solid silver;
  color: gray;
}

.barContent1 {
  font-size: 20px;
  color: #000;
  font-weight: bold;
}

.barContent3 {
  border-right: 0;
}

.barContent2 b, .barContent3 b {
  margin-left: 10px;
  font-weight: 100;
}

.btnSec2 {
  padding: 20px 40px;
  background-color: #0d53b4;
  color: white;
  position: absolute;
  left: 50%;
  border: 0;
  transform: translate(-50%, 0);
  border-radius: 5px;
  top: 50px;
  font-family: sans-serif;
  font-size: 15px;
  font-weight: 500;
}


.sec3 {  
  position: absolute;
  width: 100%;
  top: 1000px;
  left: 0;
  padding-top: 50px;
}

.sec3Subheader {
  color: #0d53b4;
  font-weight: bolder;
  text-align: center;
}

.sec3Header {  
  font-size: 40px;
  font-family: serif;
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
  width: 40%;
  margin-left: 30%;
}

.cardsContainer {
  width: 80%;
  margin: 50px 50%;
  margin-right: 0;
  transform: translate(-50%, 0);
  display: inline-flex;
}

.card {
  padding: 20px;
  margin-bottom: 50px;
}

.cardIcon img {
  width: 50px;
  height: 50px;
}
  
.cardDescription {
  margin-bottom: 50px;
}

.cardButton {
  color: #0d53b4;
  font-weight: 600;
  font-size: larger;
}



.sec4 {  
  position: absolute;
  width: 100%;
  top: 1500px;
  left: 0;
  padding: 5% 10%;
  background-color: #ecf3fa;
}

.sec4Subheader {
  color: #0d53b4;
  font-weight: bolder;
}

.sec4Header {  
  font-size: 40px;
  font-family: serif;
  margin-top: 0;
}



.sec5 {  
  position: absolute;
  width: 100%;
  top: 2000px;
  left: 0;
  display: flex;
}

.sec5 > div {
  width: 60%;
  font-size: xxx-large;
}

.sec5 > div.sec5Part1 {
  width: 40%;
  padding: 5%;
  padding-bottom: 0;
}

.sliderController {
  display: inline-flex;
  margin-bottom: 25px;
}

#sliderIndicator  {
  transform: rotate(-45deg);
  border: 1px solid silver;
  border-top: 1px solid #0d53b4;
  border-radius: 100%;
  width: 50px;
  height: 50px;
  margin-right: 25px;
  display: flex;
}

span#sliderCounter {
  transform: rotate( 
45deg
);
  width: 100%;
  line-height: 50px;
  text-align: center;
  font-size: medium;
}

#sliderText {
  font-size: initial;
}

#sliderText .sliderData {
  display: none;
}

#sliderImages img{
  display: none;
}


#sliderText .sliderHeader {
  font-size: 40px;
  font-family: serif;
  margin-top: 0;
  margin-bottom: 0;
  width: 80%;
}

#sliderText .sliderDescription {
  color: gray;
  margin-bottom: 70px;
}

#sliderImages img {
  width: 100%;
  height: 100%;
}



.sec6 {  
  position: absolute;
  width: 100%;
  top: 2500px;
  left: 0;
  background-color: #162d4c;
  color: #fff;
}


.sec6Subheader {
  margin-top: 100px;
  color: #c4ddff;
  font-weight: 600;
  text-align: center;
  font-size: larger;
}

.sec6Header {  
  font-size: 40px;
  font-family: serif;
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
  width: 40%;
  margin-left: 30%;
}

.sec6ContentChanger {
  display: flex;
  border-bottom: 1px solid #c4ddff;
}

.sec6ContentChanger > div {
  width: 33.3%;
  text-align: center;
}

.sec6ContentChanger > div > h4 {
  width: fit-content;
  margin-left: 50%;
  transform: translate(-50%, 0);
  padding: 3px 10px;
  font-weight: 300;
  white-space: nowrap;  
  margin-bottom: -2px;
}

.sec6ContentChanger > div:nth-child(1) > h4 {
  border-bottom: 2px solid #c4ddff;
}

#sec6Content1 {
  display: table-row;
  width: 90%;
  margin-left: 5%;
}

#sec6Content1 > img {
  display: table-cell;
  width: 100%;
}

#sec6Content1 > div > h3 {
  font-family: serif;
  font-size: xx-large;
}

#sec6Content1 > div > ol > li {
  margin: 10px 0;
}

#sec6Content1 > div {
  display: table-cell;
  vertical-align: middle;
  padding-right: 25px;
}